.App{
    width: 100%;
}

.commandement_list{
    .presentation_list{
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 90%;
        padding: 1vw 5%;
        h1{
            font-size: 1.6vw;
            font-family: "Roboto black";
        }
        div{
            display: flex;
            align-items: center;
            justify-content: center;
            img{
                width: 2vw;
                height: 2vw;
            }
            input{
                padding:0.6vw 1vw;
                margin-left: 1vw;
                border: 0;
                font-size: 1.1vw;
                border-radius: 0.5vw;
            }
            input::placeholder{
                font-size: 1.1vw;
                font-family: "Roboto";
            }
            input:focus{
                outline: none;
            }
        }
        
    }

    .table_list{
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
        align-items: center;
        width: 80%;
        padding: 0 10%;
        background-color: #fafafa;
        h1{
            font-size: 2vw;
            font-family: "Roboto black";
            width: 100%;
            padding: 2vw 0 0;
            text-align: center;
        }
        a{
            width: 50%;
            outline: none;
            text-decoration: none;
            color: #2c1d1d;
            div{
                display: flex;
                justify-content: center;
                align-items: center;
                margin: 0.6vw 0;
                div{
                    background-color: #6a53cf;
                    border-radius: 100%;
                    width: 3vw;
                    height: 3vw;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    margin-right: -2vw;
                    z-index: 1;
                    h1{
                        color: white;
                        font-size: 3vw;
                        padding: 0;
                    }
                }
                div.cell {
                    z-index: 0;
                    width: 90%;
                    border-radius: 0.3vw;
                    background-color: white;
                    -webkit-box-shadow: 5px 4px 3px 0px rgba(168,168,168,1);
                    -moz-box-shadow: 5px 4px 3px 0px rgba(168,168,168,1);
                    box-shadow: 5px 4px 3px 0px rgba(168,168,168,1);
                    padding: 1.2vw 0;
                    h2{
                        font-family: 'Comfortaa', cursive;
                        text-transform: capitalize;
                        font-size: 1.3vw;
                    }
                }
                
            }
        }

        
    }
}

.commandement_article{
    display: flex;
    flex-direction: column;
    justify-content: center;
    .presentation_list{
        display: flex;
        background-color: white;
        align-items: center;
        justify-content: space-between;
        width: 90%;
        padding: 1vw 5%;
        h1{
            font-size: 1.6vw;
            font-family: "Roboto black";
            text-transform: uppercase;
        }
        a{
            background-color: #6a53cf;
            padding: 1vw 2vw;
            font-size: 1vw;
            text-transform: uppercase;
            color: white;
            text-decoration:none;
            font-family: "Comfortaa"
        }
        
    }
    img{
        height: 80vh;
        margin-top: 2vw;
        width: auto;
        object-fit: contain;
    }
}